import Vue from "vue";
import VueRouter from "vue-router";

import Create from "@/components/Create.vue";
import Delete from "@/components/Delete.vue";
import List from "@/components/List.vue";
import Read from "@/components/Read.vue";
import Update from "@/components/Update.vue";
import Replyinsert from "@/components/Replyinsert.vue";
import Replyupdate from "@/components/Replyupdate.vue";
Vue.use(VueRouter);

export default new VueRouter({
  mode: "history",
  routes: [
    {
      path: "/list",
      name: "list",
      component: List,
    },
    {
      path: "/create",
      name: "create",
      component: Create,
    },
    {
      path: "/delete",
      name: "delete",
      component: Delete,
    },
    {
      path: "/read",
      name: "read",
      component: Read,
    },
    {
      path: "/update",
      name: "update",
      component: Update,
    },
    {
      path: "/replyinsert",
      name: "replyinsert",
      component: Replyinsert,
    },
    {
      path: "/replyupdate",
      name: "replyupdate",
      component: Replyupdate,
    },
  ],
});
