<template>
  <div>
    <div v-if="error">{{ error }}</div>
    <div class="form-group">
      <label for="replyUserid">작성자</label>
      <input
        type="text"
        class="form-control"
        id="replyUserid"
        ref="replyUserid"
        placeholder="작성자를 입력하세요"
        v-model="replyUserid"
      />
    </div>
    <div class="form-group">
      <label for="replyContent">답변 글</label>
      <textarea
        type="text"
        class="form-control"
        id="replyContent"
        ref="replyContent"
        placeholder="내용을 입력하세요"
        v-model="replyContent"
      ></textarea>
    </div>
    <div class="text-right">
      <button class="btn btn-primary" @click="checkHandler">등록</button>
      <button class="btn btn-primary" @click="moveList">취소</button>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "replyupdate",
  data: function() {
    return {
      qnaNo: "",
      qnaDatetime: "",
      qnaUserid: "",
      qnaTitle: "",
      qnaContent: "",
      replyUserid: "",
      replyContent: "",
      replyDatetime: "",
      error: ""
    };
  },
  methods: {
    // 입력값 체크하는 checkHandler 함수 정의 - 체크가 성공하면 createHandler 호출
    checkHandler() {
      // 사용자 입력값 체크하기
      // 작성자, 제목, 내용
      let err = true;
      let msg = "";

      !this.replyUserid &&
        ((msg = "작성자를 입력해주세요"),
        (err = false),
        this.$refs.replyUserid.focus());
      err &&
        !this.replyContent &&
        ((msg = "내용을 입력해주세요"),
        (err = false),
        this.$refs.replyContent.focus());
      // 입력된 데이터가 없을 경우 각 항목에 맞는 메세지를 출력
      if (!err) {
        alert(msg);
      }
      // 만약, 내용이 다 입력되어 있다면 createHandler 호출
      else {
        this.createHandler();
      }
    },

    // createHandler 메서드 정의
    createHandler() {
      // 로컬스토리지에 저장된 데이터 가져오기(키 이름 : board)
      axios
        .put("http://localhost:9999/happyhouse/api/qna/reply/${this.qnaNo}", {
          qnaNo: this.qnaNo,
          qnaContent: this.qnaContent,
          qnaTitle: this.qnaTitle,
          qnaUserid: this.qnaUserid,
          qnaDatetime: this.qnaDatetime,
          replyUserid: this.replyUserid,
          replyContent: this.replyContent,
          replyDatetime: new Date()
        })
        .then(response => {
          if (response.data === "success") {
            alert("답변이 수정되었습니다.");
            this.moveList();
          } else if (response.data === "fail") {
            alert("답변 수정 처리시 문제가 발생했습니다.");
          } else {
            alert("관리자만 답변을 변경 할 수 있습니다.");
          }
        })
        .catch(error => {
          this.error = error;
          console.dir(error);
        });
    },
    moveList() {
      this.$router.push("/list");
    }
  },
  created() {
    const params = new URL(document.location).searchParams;

    axios(`http://localhost:9999/happyhouse/api/qna/${params.get("qnaNo")}`)
      .then(({ data }) => {
        console.log(data.qnaNo);
        (this.qnaNo = data.qnaNo),
          (this.qnaDatetime = data.qnaDatetime),
          (this.qnaUserid = data.qnaUserid),
          (this.qnaTitle = data.qnaTitle),
          (this.qnaContent = data.qnaContent),
          (this.replyContent = data.replyContent),
          (this.replyUserid = data.replyUserid);
      })
      .catch(error => {
        console.dir(error);
        this.error = error;
      });
  }
};
</script>

<style></style>
