<template>
  <div>
    <div v-if="error">
      <pre>{{ error }}</pre>
    </div>
    <table class="table table-bordered w-50">
      <tr>
        <th>번호</th>
        <td>{{ item.qnaNo }}</td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td>{{ item.qnaUserid }}</td>
      </tr>
      <tr>
        <th>제목</th>
        <td>{{ item.qnaTitle }}</td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ getFormatDate(item.qnaDatetime) }}</td>
      </tr>
      <tr>
        <th>내용</th>
        <th colspan="2">{{ item.qnaContent }}</th>
      </tr>
    </table>

    <div v-if="second">
      <h3 style="text-align: center;">QnA에 대한 응답글</h3>
      <table class="table table-bordered w-50">
        <tr>
          <th>응답자</th>
          <th>{{item.replyUserid}}</th>
        </tr>
        <tr>
          <th>답글</th>
          <th>{{item.replyContent}}</th>
        </tr>
        <tr>
          <th>등록일자</th>
          <th v-if="second">{{ getFormatDate(item.replyDatetime) }}</th>
          <th v-if="first"></th>
        </tr>
      </table>
      <br />
    </div>
    <br />
    <div class="text-center">
      <router-link to="/list" class="btn btn-primary">목록</router-link>
      <router-link :to="'/delete?qnaNo=' + item.qnaNo" class="btn btn-primary">삭제</router-link>
      <router-link :to="'/update?qnaNo=' + item.qnaNo" class="btn btn-primary">수정</router-link>
    </div>
    <div v-if="first" class="text-center">
      <router-link :to="'/replyinsert?qnaNo=' + item.qnaNo" class="btn btn-primary">QnA답변하기</router-link>
    </div>
    <div v-if="second" class="text-center">
      <router-link :to="'/replyupdate?qnaNo=' + item.qnaNo" class="btn btn-primary">QnA수정하기</router-link>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
export default {
  data() {
    return {
      first: false,
      second: false,
      item: {},
      error: ""
    };
  },
  created() {
    const params = new URL(document.location).searchParams;
    axios(`http://localhost:9999/happyhouse/api/qna/${params.get("qnaNo")}`)
      .then(response => {
        console.dir(response);
        this.item = response.data;
      })
      .catch(error => {
        console.dir(error);
        this.error = error;
      })
      .finally(() => {
        if (this.item.replyContent == null) {
          this.first = true;
        } else {
          this.second = true;
        }
      });
  },
  methods: {
    getFormatDate(qnaDatetime) {
      // moment 라이브러리를 이용하여 날짜 정보를 년.월.일 시:분:초 의 형태로 반환한다.
      return moment(new Date(qnaDatetime)).format("YYYY.MM.DD HH:mm:ss");
    }
  }
};
</script>

<style></style>
