<template>
  <div>
    <div v-if="items.length">
      <table class="table table-bordered table-condensed">
        <colgroup>
          <col :style="{ width: '10%' }" />
          <col :style="{ width: '50%' }" />
          <col :style="{ width: '15%' }" />
          <col :style="{ width: '25%' }" />
        </colgroup>
        <tr>
          <th>번호</th>
          <th>제목</th>
          <th>작성자</th>
          <th>날짜</th>
        </tr>
        <tr v-for="(qna, index) in items" :key="index + '_items'">
          <td>{{ qna.qnaNo }}</td>
          <td>
            <router-link :to="'/read?qnaNo=' + qna.qnaNo">
              {{
              qna.qnaTitle
              }}
            </router-link>
          </td>
          <td>{{ qna.qnaUserid }}</td>
          <td>{{ getFormatDate(qna.qnaDatetime) }}</td>
        </tr>
      </table>
    </div>
    <div v-else class="text-center">게시글이 없습니다.</div>
    <div class="text-right">
      <button class="btn btn-primary" @click="movePage">등록</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
export default {
  name: "List",
  data() {
    // 목록 데이터 저장을 위한 배열 선언(배열의 이름 : items)
    return {
      items: []
    };
  },

  // created 라이프사이클 훅 선언
  created() {
    axios("http://localhost:9999/happyhouse/api/qna")
      .then(response => {
        console.dir(response);
        this.items = response.data;
      })
      .catch(error => {
        console.dir(error);
      });
  },
  methods: {
    getFormatDate(qnaDatetime) {
      return moment(new Date(qnaDatetime)).format("YYYY.MM.DD");
    },
    movePage() {
      this.$router.push("/create");
    }
  }
};
</script>

<style></style>
