<template>
  <div>삭제중...</div>
</template>

<script>
import axios from "axios";
export default {
  name: "delete",
  created() {
    const params = new URL(document.location).searchParams;
    axios
      .delete(`http://localhost:9999/happyhouse/api/qna/` + params.get("qnaNo"))
      .then((response) => {
        console.dir(response);
        alert("삭제가 완료되었습니다.");
      })
      .catch((error) => {
        console.dir(error);
        alert("삭제처리시 에러가 발생했습니다.");
      })
      .finally(() => {
        this.$router.push("/list");
      });
  },
};
</script>

