<template>
  <div>
    <div v-if="error">{{ error }}</div>
    <div class="form-group">
      <label for="writer">작성자</label>
      <input
        type="text"
        class="form-control"
        id="writer"
        ref="writer"
        placeholder="작성자를 입력하세요"
        v-model="qna_userid"
      />
    </div>
    <div class="form-group">
      <label for="title">제목</label>
      <input
        type="text"
        class="form-control"
        id="title"
        ref="title"
        placeholder="제목을 입력하세요"
        v-model="qna_title"
      />
    </div>
    <div class="form-group">
      <label for="content">내용</label>
      <textarea
        type="text"
        class="form-control"
        id="contnet"
        ref="content"
        placeholder="내용을 입력하세요"
        v-model="qna_content"
      ></textarea>
    </div>
    <div class="text-right">
      <button class="btn btn-primary" @click="checkHandler">등록</button>
      <button class="btn btn-primary" @click="moveList">목록</button>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "create",
  data: function() {
    return {
      qna_title: "",
      qna_content: "",
      qna_userid: "",
      error: ""
    };
  },
  methods: {
    // 입력값 체크하는 checkHandler 함수 정의 - 체크가 성공하면 createHandler 호출
    checkHandler() {
      // 사용자 입력값 체크하기
      // 작성자, 제목, 내용
      let err = true;
      let msg = "";

      !this.qna_userid && ((msg = "작성자를 입력해주세요"), (err = false));
      err && !this.qna_title && ((msg = "제목을 입력해주세요"), (err = false));
      err &&
        !this.qna_content &&
        ((msg = "내용을 입력해주세요"), (err = false));
      // 입력된 데이터가 없을 경우 각 항목에 맞는 메세지를 출력
      if (!err) {
        alert(msg);
      }
      // 만약, 내용이 다 입력되어 있다면 createHandler 호출
      else {
        this.createHandler();
      }
    },

    // createHandler 메서드 정의
    createHandler() {
      // 로컬스토리지에 저장된 데이터 가져오기(키 이름 : board)
      axios
        .post("http://localhost:9999/happyhouse/api/qna", {
          qnaContent: this.qna_content,
          qnaTitle: this.qna_title,
          qnaUserid: this.qna_userid
        })
        .then(response => {
          if (response.data === "success") {
            alert("등록이 완료되었습니다.");
          } else {
            alert("등록 처리시 문제가 발생했습니다.");
          }
        })
        .catch(error => {
          this.error = error;
          console.dir(error);
        })
        .finally(() => {
          this.moveList();
        });
    },
    moveList() {
      this.$router.push("/list");
    }
  }
};
</script>

<style></style>
