<template>
  <div>
    <div class="form-group">
      <label for="qnaUserid">작성자</label>
      <input
        type="text"
        class="form-control"
        id="qnaUserid"
        ref="qnaUserid"
        placeholder="작성자를 입력하세요"
        v-model="qnaUserid"
      />
    </div>
    <div class="form-group">
      <label for="qnaTitle">제목</label>
      <input
        type="text"
        class="form-control"
        id="qnaTitle"
        ref="qnaTitle"
        placeholder="제목을 입력하세요"
        v-model="qnaTitle"
      />
    </div>
    <div class="form-group">
      <label for="qnaContent">내용</label>
      <textarea
        type="text"
        class="form-control"
        id="qnaContent"
        ref="qnaContent"
        placeholder="내용을 입력하세요"
        v-model="qnaContent"
      ></textarea>
    </div>
    <div class="text-right">
      <button class="btn btn-primary" @click="checkHandler">수정</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      qnaNo: "",
      qnaDatetime: "",
      qnaUserid: "",
      qnaTitle: "",
      qnaContent: "",
    };
  },
  methods: {
    checkHandler() {
      let err = true;
      let msg = "";
      !this.qnaUserid &&
        ((msg = "작성자를 입력해주세요"),
        (err = false),
        this.$refs.qnaUserid.focus());
      err &&
        !this.qnaTitle &&
        ((msg = "제목 입력해주세요"),
        (err = false),
        this.$refs.qnaTitle.focus());
      err &&
        !this.qnaContent &&
        ((msg = "내용 입력해주세요"),
        (err = false),
        this.$refs.qnaContent.focus());

      if (!err) alert(msg);
      else this.updateHandler();
    },
    moveList() {
      this.$router.push("/list");
    },
    updateHandler() {
      axios
        .put("http://localhost:9999/happyhouse/api/qna/${this.qnaNo}", {
          qnaNo: this.qnaNo,
          qnaUserid: this.qnaUserid,
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
        })
        .then((response) => {
          console.dir(response);
          if (response.data === "success") {
            alert("수정이 완료되었습니다.");
          } else {
            alert("수정 처리시 문제가 발생했습니다.");
          }
        })
        .catch((error) => {
          console.dir(error);
          this.error = error;
        })
        .finally(() => {
          this.moveList();
        });
    },
  },
  created() {
    const params = new URL(document.location).searchParams;

    axios(`http://localhost:9999/happyhouse/api/qna/${params.get("qnaNo")}`)
      .then(({ data }) => {
        (this.qnaNo = data.qnaNo),
          (this.qnaDatetime = data.qnaDatetime),
          (this.qnaUserid = data.qnaUserid),
          (this.qnaTitle = data.qnaTitle),
          (this.qnaContent = data.qnaContent);
      })
      .catch((error) => {
        console.dir(error);
        this.error = error;
      });
  },
};
</script>

<style></style>
